import CommonCardFrame from '@/components/CommonCardFrame';
var ActivityFilterOptions = [{
  value: 0,
  label: '申请中'
}, {
  value: 1,
  label: '审批通过'
}, {
  value: 2,
  label: '审批不通过'
}];
var PowerFilterOptions = [{
  value: 0,
  label: '待审批'
}, {
  value: 1,
  label: '审批通过'
}, {
  value: 2,
  label: '审批不通过'
}];
export default {
  components: {
    CommonCardFrame: CommonCardFrame
  },
  props: {
    type: {
      required: true,
      type: String,
      default: 'activity'
    }
  },
  data: function data() {
    return {
      isActivityFilterForm: true,
      options: [],
